$mainGrey: #898989;
$mainGreen: #B6B950;
$mainDeepYellow: #E3C136;
$mainLightYellow: #EEDB80;

@font-face {
    font-family: 'AsapRegular';
    src: url('ASAP-font/Asap-Regular-webfont.eot');
    src: url('ASAP-font/Asap-Regular-webfont.eot?#iefix') format('embedded-opentype'),
        url('ASAP-font/Asap-Regular-webfont.woff') format('woff'),
        url('ASAP-font/Asap-Regular-webfont.ttf') format('truetype'),
        url('ASAP-font/Asap-Regular-webfont.svg#AsapRegular') format('svg');
    font-weight: normal;
    font-style: normal;
}

*{
    font-family: '微軟正黑體','AsapRegular',sans-serif;
}

@mixin mTAuto($mt:0) {
    margin: $mt auto;
}
@mixin lefTransXCenter() {
    left: 50%;
    transform: translateX(-50%);
}
@mixin bgSizePocNorep($bgSize:contain) {
    background-position: center;
    background-size: $bgSize;
    background-repeat: no-repeat;
}

@mixin lefTop($l:0,$t:0){
    left: $l;
    top: $t;
}

@mixin lsFz($ls:0rem,$fz:1rem){
    letter-spacing: $ls;
    font-size: $fz;
}

body, html{
    margin: 0;
}

body{
    background-color: black;
    min-width: min-content;
}

.fColorGrey{
    color: $mainGrey!important;
}
.fColorWhite{
    color: #fff!important;
}
a:link,a:visited,a:hover,a:active{
    color: unset;
    text-decoration: unset;
}
.cursorPt{
    cursor: pointer;
}
input[type="checkbox"]{
    $b:13px;
    height: $b;
    width: $b;
}

.outerContainer {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.508);
}

.bgi {
    position: relative;
    @include lefTop(50%,50%);
    transform: translate(-50%,-50%);
    width: 100%;
    max-width: 1440px;
    >img{
        height: 677px;
        left: 50%;
        transform: translateX(-50%);
        position: relative;
        width: 100%;
        filter: brightness(98%);
    }
    .innerContainer {
        position: absolute;
        width: 93%;
        @include lefTop(50%,8%);
        transform: translate(-50%,-100%);
    }
}
.footer{
    position: absolute;
    top: 92.7%;
    width: 100%;
    background-color: #fff;
    .toggleRound{
        position: relative;
        display: inline-block;
        width: 20px;
        height: 20px;
        border-radius: 10px;
        top: 5px;
        left: 25px;
        opacity: 0.8;
        background: linear-gradient($mainDeepYellow 5%,$mainGrey 25%,$mainGrey 60%,$mainDeepYellow);
    }
    .footerInner{
        color: $mainGrey;
        padding: 1% 0;
        position: relative;
        display: inline-block;
        left: 50%;
        transform: translateX(-50%);
        width: 93%;
        @include lsFz(0.1rem,1rem );
        img{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 0%;
            width: 10%;
        }
    }
}
.user {
    position: absolute;
    width: 12%;
    &>img {
        @include lefTop(0%,0% );
        width: 100%;
    }
    p {
        position: relative;
        margin: 0;
        text-align: center;
    }
    .name{
        position: absolute;
        width: 88%;
        top: 55%;
        color: #fff;
        @include lsFz(0.2rem,1.2rem );
    }
    .tokenYours{
        width: 88%;
        text-align: center;
        @include lsFz(0.15rem,1.6rem );
        margin-bottom: 2%;
        white-space: nowrap;
    }
    .token{
        color: $mainDeepYellow;
        @include lsFz(0.2rem,1.6rem );
        font-weight: 900;
    }
    .share{
        position: absolute;
        margin-top: 5%;
        margin-left: 50%;
    }
}
.introCont{
    position: absolute;
    right: 15%;
    width: 70%;
    color: $mainGrey;
    margin-top: 2%;
    h1{
        text-align: center;
        margin-top: 5%;
        margin-bottom: 6%;
        &.intro2{
            margin-top: 25%;
            line-height: 1.8em;
            animation: fInOut 2.5s 0.5s 1 both;
        }
    }
    p.intro1{
        line-height: 1.5em;    
        font-weight: 500;
        letter-spacing: .05em;
        text-align: justify;
    }
    p:nth-child(3){
        line-height: 1.2em;
    }
    span{
        font-weight: 200;
        font-size: 14px;
    }
}
@keyframes fInOut{
    0%,100%{
        opacity: 0;
    }
    10%,90%{
        opacity: 1;
    }
}

.introOuter{
    animation: fInToTop 1s 3s 1 both;
}
.introCol{
    display: inline-block;
    margin-right: 2%;
    margin-left: 1%;
    text-align: center;
    width: 29%;
    border-radius: 8px;
    background-image: url(../../img/introBgi.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position-x: center;
    transition: 0.5s;
    h3{
        margin-bottom: 16%;
    }
    >img{
        // position: absolute;
        // transform: translateX(-50%);
    }
    p{
        position: relative;
        width: 82%;
        @include mTAuto(6%);
        text-align: justify;
        &:nth-child(3){
            margin-top: 10%;
        }
    }
    &:hover{
        transform: translate(-3px,-3px);
        box-shadow: 3px 4px 4px 1px lighten(#898989,25%);
    }
}
@keyframes fInToTop{
    0%,10%{
        opacity: 0;
    }
    100%{
        transform: translateY(-80%);
        opacity: 1;
    }
}

.introIcon{
    @include mTAuto(8%);
    text-align: right;
    width: 82%;
    img{
        width: 10%;
        margin-left: 4%;
    }
}
.setOfTaskPage{
    @include mTAuto(0);
    width: 80%;
    h1{
        text-align-last: left;
    }
    h3{
        display: inline-block;
    }
    .formColumn{
        width: 75%;
        input{
            margin-bottom: 6%;
            &[type=text]{
                margin-bottom: 5%;
            }
        }
    }
}
.infoTrainingData{
    h3{
        padding-right: 6%;
    }
    p{
        font-weight: 200;
        margin-left: 6%;
    }
}
.nFoldDetail{
    margin-left: 18px;
}

.mainContent{
    position: absolute;
    right: 15%;
    width: 65%;
    color: $mainGrey;
}
.topImg{
    img{
        width: 100%;
        &.txt4{
            position: relative;
            width: 25%;
            @include lefTransXCenter();
        }
        &.txt5{
            position: absolute;
            @include lefTransXCenter();
            top: 20%;
        }
        &.ani1filesRow{
            width: 80%;
            animation: pureFIn 1.5s 0.5s 1 both;
        }
        &.ani1fileSliced{
            width: 4.8%;
            left: 70.8%;
            animation: ani1fileSlicedFIn 3s 0.5s 1 both;
        }
        &.ani2miners{
            top: 90%;
            width: 90%;
            animation: pureFIn 1s 8s 1 reverse forwards;
        }
        &.ani2file{
            @include lefTop(58%,80%);
            width: 90%;
        }
        &.ani2filesCheck0{
            opacity: 0;
            animation: fInToDownOut 4.5s 1.5s 1 backwards;
        }
        &.ani2filesCheck1{
            animation: pureFInOut 2s 4s 1 both;
        }
        &.ani2filesCheck2{
            animation: pureFInOut 2s 5s 1 both;
        }
        &.ani2filesCheck3{
            animation: pureFInOut 2s 6s 1 both;
        }
        &.ani2filesCheck4{
            animation: pureFInOut 2s 7s 1 both;
        }
        &.ani2filesCheckSingle{
            width: 6%;
            animation: ani2filesCheckSingle 3s 7.5s 1 both;
        }
    }
    .txt6{
        position: relative;
        width: 100%;
        text-align: center;
        h3,p{
            display: inline-block;
            margin: 8% 2% 6%;
        }
        p{
            margin-left: 0%;
        }
    }
    .txt7{
        width: 130%;
        height: 540px;
        overflow-y: scroll;
    }
}
@keyframes pureFIn{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
@keyframes ani1fileSlicedFIn{
    0%{
        transform: translate(-62%,0);
        opacity: 0;
    }
    50%,100%{
        transform: translate(-62%,0);
        opacity: 1;
    }
    100%{
        transform: translate(-479.5%,305%);
    }
}
@keyframes fOutToDown{
    0%,50%{
        transform: translate(-50%,0%);
        opacity: 1;
    }
    100%{
        transform: translate(-50%,160%);
        opacity: 0;
    }
}
@keyframes fInToDownOut{
    0%,20%{
        transform: translate(-50%,-120%);
        opacity: 0;
    }
    50%{
        transform: translate(-50%,0%);
        opacity: 1;
    }
    100%{
        transform: translate(-50%,0%);
        opacity: 0;
    }
}
@keyframes pureFInOut{
    0%,100%{
        opacity: 0;
    }
    20%,80%{
        opacity: 1;
    }
}
@keyframes ani2filesCheckSingle{
    0%{
        opacity: 0;
        transform: translate(-50%,0);
    }
    33%,66%{
        opacity: 1;
        transform: translate(-50%,0);
    }
    100%{
        z-index: 1;
        opacity: 1;
        transform: translate(-547%,300%);
    }

}


.txt6check{
    width: 132%;
    margin-left: -6%;
    h2{
        background-color: unset;
        color: $mainDeepYellow;
        margin: 0;
        padding: 0;
    }
    &>h2{
        display: block;
        margin: 2% 2% -2%;
    }
}
.colTxt6Check{
    display: inline-block;
    vertical-align: top;
    margin: 2% 0% 2% 2%;
    &:nth-child(2){
        margin-left: 4%;
        margin-right: 2%;
    }
    h3{
        font-weight: unset;
    }
    span{
        font-weight: 600;
        &::after{
            content: " : ";
        }
    }
}
.column{
    position: relative;
    display: inline-block;
    margin: 6% 0%;
    width: 23%;
    img{
        width: 15%;
        position: absolute;
        right: 2%;
        top: 100%;
    }
}
.chart{
    position: relative;
    padding-left: 30px;
}
.chartMention{
    position: absolute;
    background-color: #e6e6e6;
    width: 65%;
    right: -23%;
    margin-top: 12%;
    z-index: 3;
    display: none;
    padding: 0px 2px;
    text-align: left;
    p{
        margin: 4px;
    }
}
.circleContainer{
    width: 159.45px;
    position: absolute;
}
.circleMaskOuter,
.circleMaskInner{
    overflow: hidden;
    position: absolute;
    width: 80px;
    height: 160px;
}
.circleMaskInner{
    transform: rotate(0);
}
.circleBody{
    border-radius: 50%;
    position: absolute;
    width: 160px;
    height: 160px;
}
.circleMaskOuter.left{
    left: 0%;
    .circleMaskInner{
        left: 100%;
        transform-origin: 0 50%;
    }
    .circleBody{
        right: 0;
    }
}
.circleMaskOuter.right{
    right: 0px;
    .circleMaskInner{
        right: 100%;
        transform-origin: 100% 50%;
    }
    .circleBody{
        left: 0;
    }
}
.correct{
    z-index: 2;
    .circleBody{
        background-color: $mainDeepYellow;
    }
}
.err{
    z-index: 1;
    .circleBody{
        background-color: $mainLightYellow;
    }
}
.chartProgress{
    position: relative;
    margin-top: 80%;
    .chartProgItem{
        display: flex;
        justify-content: space-around;
        h4,.progressOuter,p{
            margin-top: 2.5%;
        }
        .progressOuter{
            width: 50%;
            position: relative;
            left: 0;
            transform: translateX(0);
        }
    }
    h4{
        width: 25px;
    }
}
.columnFooter{
    margin-top: 4%;
    padding-top: 3%;
    padding-bottom: 16%;
    width: 95%;
    border-top: 1px solid $mainGrey;
    text-align: center;
}
.txt7mask{
    position: absolute;
    right: -28.75%;
    width: 165.1%;
    z-index: 2;
    margin-top: -12%;
    height: 145px;
    background: linear-gradient(to top,#CDCDCD, rgba(#E6E6E6,0.95), rgba(#fff,0));
}

.textBlock{
    width: 65%;
    @include mTAuto(0);
    line-height: 1.6rem;
    @include lsFz(0.08rem,1rem );
    p{
        margin-top: 0;
        &.txt5{
            position: absolute;
            @include lefTransXCenter();
            top: 72%;
            width: fit-content;
            animation: checkItNow 1.5s 7.8s 1 both;
        }
    }
}
.progressOuter{
    position: absolute;
    @include lefTransXCenter();
    width: 12%;
    height: 10px;
    background-color: #fff;
    border-radius: 5px;
    border: 1px solid $mainGrey;
}
.progressInner{
    position: absolute;
    @include lefTop(2.8%,31%);
    height: 4px;
    width: 0;
    border-radius: 2px;
    background-color: $mainGreen;
}

.option{
    @include mTAuto(0);
    h2{
        opacity: 0;
    }
    &.txt1{
        width: 65%;
    }
    &.txt2{
        $t2: 70%;
        width: 100% + $t2;
        margin-left: -$t2/2;
    }
    &.txt3{
        width: 85%;
    }
    &.txt4{
        margin-top: 12%;
        margin-bottom: 3%;
        width: 70%;
        img.ani1monitor{
            position: relative;
            width: 15%;
        }
        img.ani1fileSingle{
            position: absolute;
            left: 17.2%;
            bottom: 24%;
            width: 6%;
            animation: ani1fileSingle 3.5s 1s 1 both;
        }
        .txt5.progressOuter{
            animation: pureFIn 2.5s 4s 1 both;
        }
        .progressInner{
            animation: txt5progressInner 2s 5.5s 1 both;
        }
    }
    &.txt6{
        width: 70%;
        margin-top: 5%;
        h3{
            margin: 4% 4% 2% 0;
        }
    }
    form{
        letter-spacing: 0.15rem;
        display: inline-flex;
        &.txt1{
            justify-content: space-between;
            width: 75%;
            label{
                white-space: nowrap;
            }
        }
        &.txt2{
            width: 75%;
        }
        &.txt3{
            display: inline-table;
            width: 75%;
            input{
                margin: 0 2% 6% 2%;
            }
            label{
                white-space: nowrap;
                letter-spacing: 0.08rem;
            }
            &:nth-child(2n)::after{
                content: br;
            }
        }
    }
}
@keyframes ani1fileSingle{
    0%,15%{
        transform: translate(0);
        opacity: 1;
    }
    65%,75%{
        transform: translate(496%,-275%);
        opacity: 1;
    }
    100%{
        transform: translate(496%,-275%);
        opacity: 0;
    }
}
@keyframes txt5progressInner{
    0%{
        width: 0;
        background-color: #BE2932;
    }
    35%{
        width: 20%;
        background-color: $mainDeepYellow;
    }
    50%{
        width: 55%;
    }
    100%{
        width: 95%;
        background-color: $mainGreen;
    }
}
@keyframes checkItNow{
    0%{
        opacity: 0;
        transform: translate(-50%,-30%)
    }
    100%{
        opacity: 1;
        transform: translate(-50%,5%)
    }
}

input[disabled]{
    $lightenRatio:1%;
    cursor: not-allowed;
    color: lighten($mainGrey,$lightenRatio);
    &+label{
        cursor: not-allowed;
        color: lighten($mainGrey,$lightenRatio);
    }
}
h2{
    display: inline-block;
    background-color: rgba($mainLightYellow, $alpha: 0.8);
    padding: 0% 2.5%;
    margin: 1.5% 2.5%;
}
.formColumn{
    letter-spacing: 0.08rem;
    width: 100%;
    display: inline-flex;
    justify-content: space-around;
    input{
        margin-bottom: 8%;
    }
}
.t2colInput{
    input:nth-of-type(1){
        $b:18px;
        height: $b;
        width: $b;
        &+label{
            font-size: $b +2px;
            color: $mainDeepYellow;
        }
    }
}
input[type="text"]{
    background-color: transparent;
    width: 16%;
    margin-bottom: 2%;
}
.txt6 input[type="text"]{
    border: none;
    width: 10%;
    text-align: center;
    color: $mainGreen;
    font-size: 1rem;
}
#slider{
    display: inline-block;
    width: 50%;
    position: absolute;
    margin-top: 4px;
    left: 30%;
    background-color: transparent;
    border: unset;
    span{
        cursor: pointer;
        border: none;
        background-color: $mainGreen;
        border-radius: 10px;
    }
}
.sliderMiddleLine{
    width: 100%;
    border: 1px solid $mainGrey;
    border-top: unset;
    margin-top: 0.4em;
}

.hint{
    @include mTAuto(0);
    letter-spacing: 0.15rem;
    form{
        position: relative;
        justify-content: flex-start;
        input{
            margin-left: 3%;
        }
        &.txt1{
            margin-top: 1%;
            left: 15.7%;
            width: 59%;
        }
    }
}
.widerHint{
    width: 100%;
    p{
        &.txt1{
            width: fit-content;
            @include mTAuto(2%);
            color: $mainDeepYellow;
        }
        &.textBlock{
            text-align: center;
        }
    }
    .txt6{
        width: 75%;
        @include mTAuto(5%);
        
    }
}
.cost{
    position: relative;
    width: 40%;
    text-align: center;
    margin-right: 8%;
    margin-bottom: 5%;
    color: $mainDeepYellow;
    img{
        position: absolute;
        z-index: 1;
        top: -5px;
        left: 26px;
        width: 20%;
        display: inline-block;
    }
    .progressOuter{
        position: relative;
        display: inline-block;
        left: 110%;
        width: 175%;
        height: 8px;
        .progressInner{
            height: 3px;
            background-color: $mainDeepYellow;
        }
    }
}

.nextBtn{
    position: absolute;
    width: 5%;
    margin-top: 36%;
    text-align: center;
    color: #fff;
    z-index: 3;
    &.last{
        left: -1%;
        &.txt6check{
            left: 5%;
        }
    }
    &.next{
        right: -1%;
        transform: scaleX(-1);
        
    }
    &.last,&.next{
        &.intro2{
            animation: pureFIn 2s 3s 1 both;
        }
        &.txt4{
            animation: pureFIn 1.5s 3.5s 1 both;
        }
        &.txt5{
            animation: pureFIn 1.5s 7.8s 1 both;
        }
        &.txt7pre{
            animation: pureFIn 1.5s 7.8s 1 both;
        }
    }
}
.share{
    position: relative;
    margin-top: 16%;
    margin-left: 6%;
}

input[value="Next >"]{
    cursor: pointer;
    position: absolute;
    width: 10%;
    right: -16%;
    margin-top: 16%;
    text-align: center;
    color: #fff;
    background-color: $mainGreen;
    padding: 1% 2%;
    letter-spacing: 0.2rem;
}

// .ani1fortress,.ani1filesRow,.ani1fileSliced,
// h1.intro2,
// .next,
// .ani2miners,.ani2file,.ani2filesCheckSingle,
input[value="Next >"],
{
    // display: block;
    display: none;
}

